.imageContainer {
    height: 150px;
    width: 150px;
    object-fit: contain;
    display: flex;
    justify-content: center;
    margin-top: auto;
}

.imgType {
    height: 10;
    object-fit: contain;
}

.thumbnailContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.thumbnailContainer img {
    width: 120px;
    height: 120px;
    object-fit: contain;
}

.pokemonName {
    font-family: 'Press Start 2P', cursive;
}


/* Icone do tipo dos Pokemon INICIO */

.wrapper {
    width: 800px;
    margin: auto auto;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(6, 1fr);
    align-content: center;
    align-items: center;
}

.icon {
    border-radius: 100%;
    height: 100px;
    width: 100px;
    margin: auto;
    transition: 200ms all;
}

.icon:hover {
    filter: saturate(200%);
    transform: scale(1.1);
    cursor: pointer;
}

.icon img {
    height: 60%;
    width: 60%;
    margin: 20%;
}

